<template lang="pug">
div(:class="classes" @click="onClick" :style="style" tabindex="0")
  svg(v-if="icon" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg")
    path(d="M12 5V19" stroke="#120405" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round")
    path(d="M5 12H19" stroke="#120405" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round")

  span(v-if="label") {{label}}
</template>

<script>
import "./Button.sass";

export default {
  name: 'my-button',
  props: {
    label: { 
      type: String,
      default: '',
    },
    icon: {
      type: Boolean,
      default: false,
    },
    theme: {
      type: String,
      default: 'filled',
      validator: function (value) {
        return ['filled', 'outline', 'floating', 'flat'].indexOf(value) !== -1;
      },
    },
    size: {
      type: String,
      default: 'medium',
      validator: function (value) {
        return ['small', 'medium', 'large'].indexOf(value) !== -1;
      },
    },
  },

  computed: {
    classes() {
      return {
        'my-button': true,
        [`my-button--theme-${this.theme}`]: true,
        [`my-button--size-${this.size}`]: true,
        'my-button--icon-only': this.icon && !this.label,
        'my-button--label-only': !this.icon && this.label,
        'my-button--icon-label': this.icon && this.label,
      };
    },
    style() {
      return {
      };
    },
  },

  methods: {
    onClick() {
      this.$emit('onClick');
    },
  },
};
</script>

